<template>
  <el-container class="myhospital dailylistingIn">
    <el-header style="height: 35px;">
      <div class="grid-content fonttop">
        <span>就诊人：</span>
        <span>*振</span>
        <span style="margin:10px">0005674</span>
        <span style="float: right;line-height: 15px;">
          <el-button class="cut">切换</el-button>
        </span>
      </div>
    </el-header>
    <el-main>
      <div class="In-time">
        <el-row :gutter="20">
          <el-col :span="4">
            <div v-on:click="left" class="grid-content bg-purple arrow">
              <i class="el-icon-arrow-left"></i>
            </div>
          </el-col>
          <el-col :span="16">
            <div class="grid-content bg-purple arrow">
              <span>{{inTime}}</span>
            </div>
          </el-col>
          <el-col :span="4">
            <div v-on:click="right" class="grid-content bg-purple arrow">
              <i class="el-icon-arrow-right"></i>
            </div>
          </el-col>
        </el-row>
      </div>

      <el-card class="box-card" style="padding: 5px 0;">
        <el-row>
          <el-col :span="8">
            <div class="grid-content bg-purple"></div>
          </el-col>
          <el-col :span="8">
            <div class="grid-content bg-purple-light">
              <p style="color:#FF7800;font-size:22px;">
                ￥
                <span>{{index}}</span>
              </p>
              <p class="myhospital_time_ft">预交金余额</p>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="grid-content bg-purple myhospital_time">
              住院中，第
              <span>{{clientTime1}}</span>天
            </div>
          </el-col>
        </el-row>
        <el-divider class="myhospital_hr"></el-divider>
        <el-row>
          <el-col :span="24">
            <div class="grid-content bg-purple-dark dailylistingIn_time">
              <span>{{clientTime}}入院</span>
            </div>
          </el-col>
        </el-row>
      </el-card>
      <!--  -->
      <div class="title" style="margin-top:10px">
        <div class="title-img"></div>
        <div class="title-font">
          <span>基本信息</span>
        </div>
      </div>
      <el-card class="box-card myhospital_LIST">
        <div>
          <el-row>
            <el-col :span="8" class="myhospital_list_left">
              <div class="grid-content bg-purple">患者姓名</div>
              <div class="grid-content bg-purple">科室</div>
              <div class="grid-content bg-purple">医生</div>
              <div class="grid-content bg-purple">流水号</div>
            </el-col>
            <el-col :span="16" class="myhospital_list_right">
              <div class="grid-content bg-purple-light">
                <span>{{}}</span>
              </div>
              <div class="grid-content bg-purple-light">
                <span>{{}}</span>
              </div>
              <div class="grid-content bg-purple-light">
                <span>{{}}</span>
              </div>
              <div class="grid-content bg-purple-light">
                <span>{{}}</span>
              </div>
            </el-col>
          </el-row>
        </div>
      </el-card>
      <div class="title" style="margin-top:10px">
        <div class="title-img"></div>
        <div class="title-font">
          <span>费用明细</span>
        </div>
      </div>
      <!--  -->
    </el-main>
  </el-container>
</template>
<style lang="stylus" scoped>
@import '../../assets/css/outpatient.css';
</style>
<script>
export default {
  data() {
    return {
      clientName: "刘溜",
      clientAge: "511",
      clientRoom: "00024578",
      clientTime: "2020-4-13",
      inTime: [],
      clientTime1: "3",
      index: "",
      //
      tableData: [
        {
          name: "床位费",
          sum: 40.0
        },
        {
          name: "中成药费",
          sum: 40.001
        },
        {
          name: "床位费",
          sum: 40.0
        },
        {
          name: "床位费",
          sum: 40.0
        }
      ]
    };
  },
  mounted() {
    this.time();
    console.log(this.time());
    
  },
  methods: {
    // 日期
    time() {
      var new_Date = new Date(); //当前时间
      var timesStamp = new_Date.getTime(); //获取毫秒
      var day = new_Date.getDay(); //当前系统时间中的星期值
      var weeks = [
        "星期日",
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六"
      ];
      var inTime = [];
      for (var i = 0; i < 15; i++) {
        day++;
        var week = weeks[day - 1];
        if (day == 7) {
          day = 0;
        }
        // new Date(timesStamp + 24 * 60 * 60 * 1000 * (i-3) 当前时间+(i+3)
        inTime.push(
          new Date(timesStamp + 24 * 60 * 60 * 1000 * (i - 7))
            .toLocaleDateString()
            .replace(/[年月]/g, "-")
            .replace(/[日上下午]/g, "") + week
        );
      }
    

      // }
      var index = inTime.length - 8;
      this.index = index;
      this.inTime = inTime[index];
  
      return inTime;
    },
    left: function() {
      this.index--
      // var index = inTime.length - 8;
      // this.index = index;
      // this.inTime = inTime[index];
    },
    right: function() {
      var index = this.time().length - 8;
      this.inTime = this.time()[index];
      console.log(index);
      this.index ++;
      

    //   for (let i = 0; i < this.time().length; i++) {
    //     const element = this.time()[i];
    //     console.log(i);
    // }
    }
  }
};
</script>



